import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Router } from '@angular/router'

@Component({
  selector: 'app-gl2',
  templateUrl: './gl2.component.html',
  styleUrls: ['./gl2.component.css']
})
export class Gl2Component implements OnInit {

  constructor(private location: Location,private router:Router) { }
public url:any;
  ngOnInit() {
    this.log
    this.submit
  }
  
public log(x){
  console.log(x)

}
public submit(f){
  console.log(f)
}

public onSwipeRight(event){
this.url=this.router.navigate(['/gl1'])
return this.url
}
  
public onSwipeLeft($event){
this.url=this.router.navigate(['/gl3'])
return this.url
}
  public goBack(){
    this.location.back();
    //window.history.back()

}
}
