import { Component, OnInit } from '@angular/core';
import{FacebookApiService} from "../../facebook-api.service";

@Component({
  selector: 'app-instapp',
  templateUrl: './instapp.component.html',
  styleUrls: ['./instapp.component.css']
})
export class InstappComponent implements OnInit {
  public insta:any=[];
  item:any=[];
  constructor(private fbook:FacebookApiService) { }

  ngOnInit() {
  this.fbook.login()
  this.fbook.loginSatus()
  this.getdata()
  }

  public getdata(){
    this.fbook.stream.subscribe(data=>{
      this.insta=data
      for(let i=0;i<this.insta.length;i++){
        this.item=this.insta[i]
        //console.log("i",this.item)
      }
      
      console.log('in',this.insta)
      
      
      

      })
     
  
    }

}
